<template>
  <div class="mainContents">
    <h1 id="profile-top">Contact</h1>
    <div class="introPhotoWrap">
      <img class="introPhoto" src="../assets/img/contact-img.png" />
    </div>
    <h2></h2>
    <div class="textWrap">
      <a>
        <br>
        <span id="target">hm.hzm331@gmail.com</span>
      </a>
    </div>
    <button class="GoBtn" @click="writeToClipboard()"><span></span></button>
  </div>
</template>

<script>
export default {
  name: 'ContactPage',
  methods: {
    writeToClipboard() {
      const copyText = this.$el.querySelector('#target').textContent
      navigator.clipboard
        .writeText(copyText)
        .then(() => {
          alert('')
        })
        .catch(e => {
          console.error(e)
        })
    }
  }
}
</script>

